import React from "react";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import styled from "styled-components";

import { FlexVertical } from "../../shared";
import { PageHeader } from "../PageHeader/PageHeader";

const MainBlock = styled(FlexVertical)`
  align-items: center;
  width: 100%;
`;

const Content = styled(FlexVertical)`
  width: 70%;
`;

const _Aromatherapy = () => {
  const { t } = useTranslation("header");
  const { pathname } = useLocation();

  const title = t(`navs.${pathname.substring(1)}`);
  return (
    <MainBlock>
      <PageHeader title={title}></PageHeader>
      <Content>
        <p>
          Le terme a été utilisé pour la première fois par le chimiste
          René-Maurice Gattefossé en 1935.
          <br />
          Il vient du latin « aroma », grec « ἄρωμα - arôma » pour « arôme » et
          du grec « θεραπεία - therapeia » pour « soin, cure ».
          <br />
          L’aromathérapie consiste en l'utilisation de composés aromatiques
          extraits de plantes, les huiles essentielles, à des fins médicales.
        </p>
        <h3>Une huile essentielle :</h3>
        <p>
          L’huile essentielle est la fraction odorante volatile extraite des
          organes sécréteurs d’un végétal.
          <br />
          <b>
            C’est le parfum concrétisé de la plante, un véritable concentré.
          </b>
          <br />
          <p>
            Lorsque vous épluchez une orange, ou que vous traversez une forêt de
            pins ou d’eucalyptus ce sont leurs HE que vous sentez.
          </p>
          <p>
            Elles peuvent être extraites de différentes parties d’un végétal :
            <ul>
              <li>les feuilles (eucalyptus)</li>
              <li>les fleurs (rose – camomille)</li>
              <li>l’écorce (la cannelle)</li>
              <li>les bois (le cèdre)</li>
              <li>le zeste (citron)</li>
              <li>
                et bien d’autres encore : les graines, les baies, les fruits, le
                bulbe...
              </li>
            </ul>
          </p>
          Une huile essentielle est un extrait liquide, concentré et complexe,
          obtenu par distillation par entraînement à la vapeur d’eau de plantes
          aromatiques ou d’organe de cette plante (fleur, feuille, bois, racine,
          écorce, fruit,...). Une huile essentielle est donc l’essence distillée
          de la plante aromatique. Elle est composée d’une centaine de molécules
          actives pour la santé au quotidien.
        </p>
      </Content>
    </MainBlock>
  );
};

export const Aromatherapy = _Aromatherapy;
